
body {
  height: 100%;
  width: 100%;
}
.xp-grid {
  position: fixed;
  top: 80px;
  left: 0;
  width: 100%;
  text-align: center;
}
.xp-cellContainer {
  display: inline-block;
  width: auto;
  height: auto;
  border-bottom: solid #4d4d4d 1px;
  border-right: solid #4d4d4d 1px;
}
.xp-cellContainer div>div {
  font-size: 1rem;
  float: left;
  vertical-align: middle;
  border-top: solid #4d4d4d 1px;
  border-left: solid #4d4d4d 1px;
}
.xp-col {
  width: 100%;
}
.xp-information {
  position: fixed;
  bottom: 0px;
  width: 100%;
}
.lever {
  position: fixed;
  bottom : 150px;
  left: 30px;
}
.lever .button {
  position: absolute;
}
.arrow-up {
  top: 0;
  left: 40px;
}
.arrow-right {
  top: 40px;
  left: 80px;
}
.arrow-down {
  top: 80px;
  left: 40px;
}
.arrow-left {
  top: 40px;
  left: 0;
}



